import React, { useState, useEffect } from "react";
import Button from "@mui/material/Button";
import {
  BrowserRouter as Router,
  Switch,
  Route,
  Link,
  withRouter,
} from "react-router-dom";

import "./buttons_header.css";
import { useCheats } from "hooks/useCheats";

const Buttons_Header = ({ game }) => {
  const { currentgame, setCurrentgame, getCategories, categories } =
    useCheats(false);

  useEffect(() => {
    // getCategories(); //Se hace en la página cheats.jsx
    setCurrentgame(game);
  }, [game]);

  return (
    <div className="buttons_header">
      <Button
        color="default"
        className={
          currentgame == "all" ? "button_header active_button" : "button_header"
        }
        variant="outlined"
        to="/cheats/all"
        component={Link}
      >
        All Games
      </Button>
      {categories
        ? categories.map((c, index) =>
            c.active ? (
              <Button
                className={
                  currentgame == c.url
                    ? "button_header active_button"
                    : "button_header"
                }
                variant="outlined"
                to={"/cheats/" + c.url}
                component={Link}
                key={index}
                color="default"
              >
                {c.name}
              </Button>
            ) : null
          )
        : "no hay categorias"}
    </div>
  );
};

export default Buttons_Header;
