import React, { useState, useEffect } from "react";
import { useParams } from "react-router-dom";
import "./cheats.css";
import Header_Pages from "components/General/header_pages/header_pages";
import Buttons_Header from "components/cheats/buttons_header/buttons_header";
import { useCheats } from "hooks/useCheats";
import Cheat_List from "components/cheats/cheat_list/cheat_list";
import AIRPagination from "components/cheats/cheat_list/pagination";
import AIRalert from "components/General/alert/alert";
import AIRSearchInput from "components/General/search-input/search-input";

export default function Cheats() {
  let { gameID } = useParams();
  const [cheatsFilered, setCheatsFiltered] = useState([]);
  const [currentCategorie, setCurrentCategorie] = useState({
    url: "all",
    name: "All Cheats",
  });

  const {
    cheats,
    currentgame,
    loadingCheats,
    ok,
    error,
    getCategories,
    categories,
  } = useCheats(false);
  const [currentPage, setCurrentPage] = useState(1);
  const [searchText, setSearchText] = useState("");
  const [cheatsPerPage] = useState(6);
  const [loadingFilters, setloadingFilters] = useState(false);

  useEffect(() => {
    if (gameID !== "all") {
      setloadingFilters(true);
      setTimeout(() => {
        setCheatsFiltered(cheats.filter(filtroPorSearch).filter(filtroPorGame));
        setloadingFilters(false);
      }, 100);
    } else {
      setloadingFilters(true);
      setTimeout(() => {
        setCheatsFiltered(cheats.filter(filtroPorSearch));
        setloadingFilters(false);
      }, 100);
    }
  }, [currentgame, loadingCheats, searchText]); //añadiendo tambien loadingcheats solucionamos el error de que no cargaba a actualizar la pagina (tardaba mas en recoger los cheats)

  useEffect(() => {
    getCategories();
    setCurrentCategorie(categories?.find((c) => c.url == gameID));
  }, [gameID]);

  //filtro por categoria
  const filtroPorGame = (cheat) => {
    if (cheat.category === gameID) {
      return true;
    } else {
      return false;
    }
  };

  //filtro por search
  const filtroPorSearch = (cheat) => {
    let cheat_name = cheat.name.toLowerCase();
    console.log(cheat_name);
    if (cheat_name.includes(searchText.toLowerCase())) {
      return true;
    } else {
      return false;
    }
  };

  //get current cheats
  const indexOfLastCheat = currentPage * cheatsPerPage;
  const indexOfFirstCheat = indexOfLastCheat - cheatsPerPage;
  const currentCheats = cheatsFilered.slice(
    indexOfFirstCheat,
    indexOfLastCheat
  );

  //Change Page
  const paginate = (pageNumber) => {
    setCurrentPage(pageNumber);
  };

  return (
    <>
      <Header_Pages
        page={currentCategorie?.name ? currentCategorie?.name : "All games"}
      />
      <AIRSearchInput
        searchText={searchText}
        setSearchText={setSearchText}
        category={currentCategorie?.name ? currentCategorie?.name : "All games"}
        results={cheatsFilered.length}
      />
      <Buttons_Header game={gameID} />
      <Cheat_List
        cheatsFiltered={currentCheats}
        loadingCheatsFiltered={loadingFilters}
      />
      <AIRPagination
        totalCheats={cheatsFilered.length}
        cheatsPerPage={cheatsPerPage}
        paginate={paginate}
      />
      <AIRalert visible={error.status} text={error.msg} type="error"></AIRalert>
      <AIRalert visible={ok.status} text={ok.msg} type="success"></AIRalert>
    </>
  );
}
