import React from "react";
import { makeStyles } from "@mui/styles";
import Pagination from "@mui/material/Pagination";

export default function AIRPagination({
  totalCheats,
  cheatsPerPage,
  paginate,
}) {
  const totalpages = Math.ceil(totalCheats / cheatsPerPage);

  return (
    <div className="pagination_div">
      {totalCheats < 1 ? null : (
        <Pagination
          count={totalpages}
          color="primary"
          onChange={(e, value) => paginate(value)}
        />
      )}
    </div>
  );
}
